function qqlized(cc){
    var str='';
    for(var i=0;i<cc.length;i++){
        if(ftPYStr().indexOf(cc.charAt(i))!=-1)
            str+=qqPYStr().charAt(ftPYStr().indexOf(cc.charAt(i)));
        else if(simpPYStr().indexOf(cc.charAt(i))!=-1)
            str+=qqPYStr().charAt(simpPYStr().indexOf(cc.charAt(i)));
        else
            str+=cc.charAt(i);
    }
    return str;
}
